<template>
    <div>
         <loading :active.sync="isLoading"></loading>
            <div class="text-right mt-4">
            <button class="btn btn-primary" @click.prevent="openModal(true)">新增優惠券</button>
            </div>
            <table style="width:60%; margin: 0 auto;"  class="table mt-4">
            <thead>
                <tr>
                <th>優惠券名稱</th>
                <th width="140">折價單位</th>
                <th width="140">票券到期日</th>
                <th width="140">啟用狀態</th>
                <th width="140" class="text-center">編輯</th>
                <!-- <th class="text-center">原價</th>
                <th class="text-center">特價</th> -->
                <!-- <th>啟用狀態</th> -->
                <!-- <th class="text-center">編輯</th> -->
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in coupons" :key ="item.id">
                <td>{{ item.title }}</td>
                <td class="text-center">{{ item.percent }}</td>
                <td>{{ formatDate[index] }}</td>
                <!-- <td class="text-right"></td>
                <td class="text-right"></td> -->
                <td class="text-center">
                     <span v-if="item.is_enabled" class="text-success">啟用</span>
                     <span v-else class="text-danger">未啟用</span>
                </td>
                <td>
                    <button
                    class="btn btn-outline-primary btn-sm mx-1"
                    @click.prevent="openModal(false , item)"
                    >編輯</button>
                    <button
                    class="btn btn-outline-danger btn-sm mx-1"
                    @click.prevent="openDelModal(item)"
                    >刪除</button>
                </td>
                </tr>
            </tbody>
        </table>

        <!-- addCouponModal -->
            <div
            class="modal fade"
            id="couponModal"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
            >
                <div class="modal-dialog modal-lg" role="document">
                    <div class="modal-content border-0">
                        <div class="modal-header bg-dark text-white">
                            <h5 class="modal-title" id="exampleModalLabel">
                            <span>新增優惠券</span>
                            </h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                    <div class="modal-body">
                        <div class="row">
                        <div class="col-md-12">
                            <div class="form-group">
                            <label for="title">優惠券名稱</label>
                            <input
                                v-model="tempCoupon.title"
                                type="text"
                                class="form-control"
                                id="title"
                                placeholder="請輸入優惠券名稱"
                            >
                            </div>
                            <div class="form-group">
                                <label for="due_date">優惠到期日</label>
                                <input
                                v-model="tempCoupon.due_date"
                                type="date"
                                class="form-control"
                                id="due_date"
                                placeholder="請輸入到期日"
                                >
                            </div>
                            <div class="form-group">
                                <label for="percent">折扣單位</label>
                                <input
                                v-model="tempCoupon.percent"
                                type="number"
                                class="form-control"
                                id="percent"
                                placeholder="請輸入折扣單位"
                                >
                            </div>
                            <div class="form-group">
                            <label for="title">優惠碼</label>
                            <input
                                v-model="tempCoupon.code"
                                type="text"
                                class="form-control"
                                id="code"
                                placeholder="請輸入優惠券碼"
                            >
                            </div>
                            <hr>
                            <div class="form-group">
                            <div class="form-check">
                                <input
                                class="form-check-input"
                                type="checkbox"
                                v-model="tempCoupon.is_enabled"
                                :true-value="1"
                                :false-value="0"
                                id="is_enabled"
                                >
                                <label class="form-check-label" for="is_enabled">是否啟用</label>
                            </div>
                            </div>
                        </div>
                        </div>
                    </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click.prevent="updateCoupon">確認</button>
                </div>
                </div>
            </div>
        </div>

        <!-- delmodal -->
         <div
            class="modal fade"
            id="delCouponModal"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
            >
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除優惠券</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除
                    <strong class="text-danger">{{ tempCoupon.title }}</strong> (刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-danger" @click.prevent="deleteCoupon">確認刪除</button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from "jquery"
export default {
    data(){
        return{
            coupons:[],
            tempCoupon:{},
            isNew: false,
            isLoading:false,
        }
    },
    computed:{
        formatDate(){
            const vm = this
            let formatDates = []
            vm.coupons.forEach(item=>{
                let dates = new Date(item.due_date * 1000)
                let years = dates.getFullYear() + '-'
                let months = (dates.getMonth()+1 < 10 ? '0'+(dates.getMonth()+1) : dates.getMonth()+1) + '-'
                let Dates = (dates.getDate() < 10 ? '0' + (dates.getDate()) : dates.getDate())
                formatDates.push(years + months + Dates) 
                item.due_date = years + months + Dates
            })
            return formatDates
        }
    },
    methods:{
        getCoupons(page = 1){
            const vm = this
            let api = `${process.env.APIPATH}/api/${
            process.env.CUSTOMEPATH
            }/admin/coupons?page=${page}`
            vm.isLoading = true
            vm.$http.get(api).then(res =>{
                vm.coupons = res.data.coupons
                vm.isLoading = false
            })
        },
        openModal(isNew , item){
            const vm = this
            if(isNew){
                vm.tempCoupon = {}
                vm.isNew = true
            }else{
                vm.tempCoupon = Object.assign({},item)
                vm.isNew = false
            }
            $('#couponModal').modal('show')
        },
        updateCoupon(){
            const vm = this
            let httpMethod = 'post'
            let api = `${process.env.APIPATH}/api/${
                process.env.CUSTOMEPATH
                }/admin/coupon`
                let coupon = {
                    title: vm.tempCoupon.title,
                    code: vm.tempCoupon.code,
                    percent: vm.tempCoupon.percent,
                    is_enabled : vm.tempCoupon.is_enabled,
                    due_date : Math.floor(new Date(vm.tempCoupon.due_date) / 1000)
                }
                if(vm.tempCoupon.is_enabled === 0){
                    vm.$set(coupon, 'due_date', Math.floor(new Date() / 1000))
                }else{
                    vm.$set(coupon, 'due_date' , Math.floor(new Date(vm.tempCoupon.due_date) / 1000))
                }
            if(!vm.isNew){
                httpMethod = 'put'
                api = `${process.env.APIPATH}/api/${
                process.env.CUSTOMEPATH
                }/admin/coupon/${vm.tempCoupon.id}`
            }

            vm.$http[httpMethod](api , { data: coupon }).then(res =>{
                if(res.data.success){
                    vm.getCoupons()
                    $('#couponModal').modal('hide')
                    this.$bus.$emit("messsage:push",res.data.message, "success")
                }else{
                     vm.getCoupons()
                    $('#couponModal').modal('hide')
                    this.$bus.$emit("messsage:push",res.data.message, "danger")
                }
            })
        },
        openDelModal(item){
            const vm = this
            vm.tempCoupon = Object.assign({},item)
            $('#delCouponModal').modal('show')
        },
        deleteCoupon(){
            const vm = this
            let api = `${process.env.APIPATH}/api/${
                process.env.CUSTOMEPATH
                }/admin/coupon/${vm.tempCoupon.id}`
            vm.$http.delete(api).then(res => {
                if(res.data.success){
                    $('#delCouponModal').modal('hide')
                    this.$bus.$emit("messsage:push",res.data.message, "success")
                    vm.getCoupons()
                }else{
                    $('#delCouponModal').modal('hide')
                    this.$bus.$emit("messsage:push",res.data.message, "danger")
                    vm.getCoupons()
                }
            })
        }
        
    },
    created(){
        this.getCoupons()
    }
}
</script>


