<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    
    <div class="row mt-4">
      <div class="col-md-12">
        <input  type="text" v-model="inputTitle">
      </div>
        <div class="col-md-12">
          <div class="row">
            <div class="form-group col-md-3">
                <select class="form-control my-3" v-model="input.type">
                      <option :value="null" disabled>--請選擇--</option>
                      <option v-for="(item , index) in typeList.sort" :key="index">{{ item }}</option>
                </select>
            </div>
            <div class="form-group col-md-9 ">
                <select class="form-control my-3" v-model="input.title">
                      <option :value="null" disabled>--請選擇--</option>
                      <option v-for="(item , index) in titleList.sort" :key="index">{{ item }}</option>
                </select>
            </div>
          </div>
      </div>
        <div class="col-md-4 mb-4" v-for="item in paginations[currentPage]" :key="item.id">
          <div class="card border-0 shadow-sm" v-if = "!content">
            <div
              style="height: 150px; background-size: cover; background-position: center"
              :style="{backgroundImage: `url(${item.image})`}"
            ></div>
            <div class="card-body">
              <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
              <h5 class="card-title">
                <a href="#" class="text-dark">{{item.title}}</a>
              </h5>
              <p class="card-text">{{item.content}}</p>
              <div class="d-flex justify-content-between align-items-baseline">
                <div class="h5" v-if=" !item.price ">現在只要 {{ item.origin_price | currency }}</div>
                <del class="h6" v-if=" item.price && item.origin_price != 0">原價 {{ item.origin_price | currency }}</del>
                <div class="h5" v-if=" item.price ">現在只要 {{ item.price | currency }}</div>
              </div>
            </div>
            <div class="card-footer d-flex">
              <button
                type="button"
                class="btn btn-outline-secondary btn-sm"
                @click.prevent="getProduct(item.id)"
              >
                <i class="fas fa-spinner fa-spin" v-if="item.id == status.loadingItem"></i>
                查看更多
              </button>
              <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
                  @click.prevent="addtoCart(item.id)">
                <i class="fas fa-spinner fa-spin" v-if="item.id == status.loadingItem"></i>
                加到購物車
              </button>
            </div>
          </div>
        </div>

        <div class="col-md-3 mb-4">
          <div class="card border-0 shadow-sm" v-if ="content">
            <div
              style="height: 150px; background-size: cover; background-position: center"
              :style="{backgroundImage: `url(${content.image})`}"
            ></div>
            <div class="card-body">
              <span class="badge badge-secondary float-right ml-2">{{ content.category }}</span>
              <h5 class="card-title">
                <a href="#" class="text-dark">{{content.title}}</a>
              </h5>
              <p class="card-text">{{content.content}}</p>
              <div class="d-flex justify-content-between align-contents-baseline">
                <div class="h5" v-if=" !content.price ">現在只要 {{ content.origin_price | currency }}</div>
                <del class="h6" v-if=" content.price && content.origin_price != 0">原價 {{ content.origin_price | currency }}</del>
                <div class="h5" v-if=" content.price ">現在只要 {{ content.price | currency }}</div>
              </div>
            </div>
            <div class="card-footer d-flex">
              <button
                type="button"
                class="btn btn-outline-secondary btn-sm"
                @click.prevent="getProduct(content.id)"
              >
                <i class="fas fa-spinner fa-spin" v-if="content.id == status.loadingItem"></i>
                查看更多
              </button>
              <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
                  @click.prevent="addtoCart(content.id)">
                <i class="fas fa-spinner fa-spin" v-if="content.id == status.loadingItem"></i>
                加到購物車
              </button>
            </div>
          </div>
        </div>
    </div>
     <nav aria-label="Page navigation example" v-if="content === null && inputTitle === ''">
            <ul class="pagination">
                <li class="page-item" :class="{'disabled' : currentPage === 0}">
                  <a class="page-link" @click.prevent="currentPage = currentPage - 1" href="#" aria-label="Previous">
                      <span aria-hidden="true">&laquo;</span>
                  </a>
                </li>
                <li class="page-item" :class="{'active': currentPage === page -1}" v-for="page in paginations.length" :key="page">
                    <a class="page-link"  href="#" @click.prevent="currentPage = page - 1">{{ page }}</a>
                </li>
                <li class="page-item" :class="{'disabled' : currentPage === paginations.length -1}">
                  <a class="page-link"  @click.prevent="currentPage = currentPage + 1" href="#" aria-label="Next">
                      <span aria-hidden="true">&raquo;</span>
                  </a>
                </li>
            </ul>
    </nav>
    <!-- <Page :pagination="pagination" v-on:getPageProducts="getProducts" v-if="!content"></Page> -->
    <!-- <div style="width:60%; margin: 100px auto;" v-if="cartLen !== 0">
    <p class="text-center text-primary h1">購物車清單模擬</p>
