    <!-- userfrom -->

  <div class="my-5 row justify-content-center">
    <form class="col-md-6" @submit.prevent="createOrder">
      <div class="form-group">
        <label for="useremail">Email</label>
        <input v-validate="'required|email'" type="email" class="form-control" name="email" id="useremail"
          v-model="form.user.email" 
          :class="{'is-invalid' : errors.has('email')}"
          placeholder="請輸入 Email" required>
        <span class="text-danger"  v-if="errors.has('email')"> {{errors.first('email')}} </span>
      </div>
    
      <div class="form-group">
        <label for="username">收件人姓名</label>
        <input v-validate="'required'" type="text" class="form-control" name="name" id="username"
          v-model="form.user.name" 
          :class="{'is-invalid' : errors.has('name')}"
          placeholder="輸入姓名">
        <span class="text-danger" v-if="errors.has('name')">必須輸入姓名</span>
      </div>
    
      <div class="form-group">
        <label for="usertel">收件人電話</label>
        <input v-validate="'required|phone'" type="tel" class="form-control" id="usertel" name="phone"
               v-model="form.user.tel" 
               :class="{'is-invalid':errors.has('phone')}"
               placeholder="請輸入電話">
        <span class="text-danger" v-if="errors.has('phone')">{{ errors.first('phone') }}</span>
      </div>
    
      <div class="form-group">
        <label for="useraddress">收件人地址</label>
        <input v-validate="'required'" type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
               :class="{'is-invalid':errors.has('address')}"
               placeholder="請輸入地址">
        <span class="text-danger" v-if="errors.has('address')">地址不能為空</span>
      </div>
    
      <div class="form-group">
        <label for="comment">留言</label>
        <textarea name="comment" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"
                  v-validate="'required'"
                  :class="{'is-invalid' : errors.has('comment')}"
                  >
        </textarea>
        <span class="text-danger" v-if="errors.has('comment')">內容不得為空</span>
      </div>
      <div class="text-right">
        <button class="btn btn-danger">送出訂單</button>
      </div>
    </form>
</div>




    <!-- end -->
            <!-- <button 
              style="position:fixed; bottom:0; right:0; "
              type="button"
              class="btn btn-secondary"
              @click.prevent="getCart()"
            >
              購物車
            </button> -->
            <div style="position:fixed; bottom:120px; right:90px; z-index:9999; border-radius:50px; background-color:red; width:20px; height:20px; text-align:center; color:white; font-weight:bold;">{{ cartLen }}</div>

            <a href="#" style="position:fixed; bottom:0; right:0;"
             @click.prevent="getCart(true)">
                <img src="../../assets/cart.png" alt="購物車">
            </a>

<!--DetailModal-->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.image" class="img-fluid" alt>
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price }} 元</div>
              <del class="h6" v-if="product.price && product.origin_price != 0">原價 {{ product.origin_price }} 元</del>
              <div class="h4" v-if="product.price">現在只要 {{ product.price }} 元</div>
            </div>
            <select name class="form-control mt-3" v-model="product.num">
              <option value="0" selected disabled>--請選擇數量--</option>
              <option :value="num" v-for="num in 10" :key="num">選購 {{num}} {{product.unit}}</option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              小計
              <strong>{{ product.num * product.price }}</strong> 元
            </div>
            <button
              type="button"
              class="btn btn-primary"
              @click="addtoCart(product.id, product.num)"
            >
              <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

  <!--CartModal-->
  <div
      class="modal fade"
      id="CartModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">購物車清單</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div v-if="cartLen !== 0">
            <p class="text-center text-primary h1">購物車清單模擬</p>
            <table class="table table-sm mt-4">
              <thead>
                <tr>
                  <th> </th>
                  <th>商品類型</th>
                  <th>數量</th>
                  <th class="text-right">單位</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in cartList.carts" :key="item.id">
                    <td>
                        <button
                        class="btn btn-outline-danger btn-sm mx-1"
                            @click.prevent="delCart(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="item.id === status.loadingItem"></i>
                        <i class="fas fa-trash-alt" v-else></i>
                        </button>
                    </td>
                    <td>{{ item.product.title }}
                          <div v-if="item.final_total !== item.total" class="text-success">
                            以套用優惠券
                          </div>
                    </td>
                    <td>{{ item.qty }} {{  item.product.unit }}</td>
                    <td class="text-right">{{ item.final_total | currency }}</td>
                </tr>
                <tr>
                    <td></td>
                    <td></td>
                    <td class="text-right">總計</td>
                    <td class="text-right">{{ cartList.total | currency }}</td>
                </tr>
                <tr class="text-success" v-if="cartList.final_total !== cartList.total">
                    <td></td>
                    <td></td>
                    <td class="text-right">折扣價</td>
                    <td class="text-right">{{ cartList.final_total | currency  }}</td>
                </tr>
              </tbody>
            </table>
            <div class="input-group mb-3 input-group-sm">
                <input class="form-control" type="text" placeholder="請輸入優惠碼" v-model = "coupon_code">
                <div class="input-group-append">
                    <button class="btn btn-outline-secondary" type="button"
                        @click.prevent="addCouponCode">
                    加入優惠券
                    </button>
                </div>
            </div>    
            </div>
            <div v-else class="noChoice">
                <p class="text-center text-danger h1">您尚未選擇任何商品</p>
            </div>
          </div>
        </div>
      </div>
    </div>



  </div>
</template>


<script>
import Page from "../Pagination";
import $ from "jquery";
export default {
  data() {
    return {
      allProducts:[],
      isLoading: false,
      product: {},
      status:{
          loadingItem:''
      },
      cartList:[],
      cartLen: 0,
      coupon_code:'',
      form:{
        user:{
          email:'',
          name:'',
          tel:'',
          address:'',
        },
        message:''
      },
      input:{
        type: null,
        title:null
      },
      inputTitle:'',
      currentPage:0
    };
  },
  computed:{
    typeList(){
      let obj = {
        sort: [],
        map : {}
      }
      this.allProducts.forEach(({category,title,content,description,id,image,is_enabled,num,origin_price,price,unit}) => {
        if(!obj.map[category]){
          obj.sort.push(category)
          obj.map[category] = {
            sort: [],
            map: {}
          }
        }
        obj.map[category].sort.push(title)
        obj.map[category].map[title] = {category,title,content,description,id,image,is_enabled,num,origin_price,price,title,unit}
      })
      return obj
    },
    titleList(){
      this.input.title = null
      if(this.input.type){
        return this.typeList.map[this.input.type]
      }else{
        return []
      }
    },
    content(){
      if(this.input.title){
        return this.titleList.map[this.input.title]
      }else{
        return null
      }
    },
    filterProducts(){
      let vm = this
      return  vm.allProducts.filter(item => {
                return item.title.match(vm.inputTitle)
              })
    },
    paginations(){
      const vm = this
      const newData = []
      if(vm.inputTitle){
        vm.allProducts.forEach((item , i) =>{
          if(i % 6 === 0){
            newData.push(vm.filterProducts)
          }
        })
      }else{
      // pages.total_pages = Math.ceil( vm.products.length / 2 )
        vm.allProducts.forEach((item , i) => {
          if(i % 6 === 0){
            newData.push([])
          }
          let page = parseInt( i / 6 )
          newData[page].push(item)
        })
      }
      return newData
    }

  },
  methods: {
    getAllProducts() {
      const vm = this;
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMEPATH
      }/products/all`;
      // const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEPATH}/products/all`

      vm.$http.get(api).then(res => {
        vm.allProducts = res.data.products
        vm.allProducts.forEach((item , index) =>{
          if(item.is_enabled !== 1){
            vm.products.splice(index,1)
          }
        })
      });
    },
    // getProducts(page = 1) {
    //   const vm = this;
    //   let api = `${process.env.APIPATH}/api/${
    //     process.env.CUSTOMEPATH
    //   }/products?page=${page}`;
    //   // const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEPATH}/products/all`

    //   vm.isLoading = true;
    //   vm.$http.get(api).then(res => {
    //     vm.isLoading = false;
    //     vm.products = res.data.products
    //     vm.products.forEach((item , index) =>{
    //       if(item.is_enabled !== 1){
    //         vm.products.splice(index,1)
    //       }
    //     })
    //     vm.pagination = res.data.pagination;
    //     let total_Page = Math.ceil(vm.products.length / 9); //總頁數
    //   });
    // },
    getProduct(id) {
      const vm = this;
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMEPATH
      }/product/${id}`;
      vm.status.loadingItem = id;
      vm.$http.get(api).then(res => {
        vm.product = res.data.product;
        vm.product.num = 0
        $('#productModal').modal('show')
        vm.status.loadingItem = ''
      });
    },
    addtoCart(id , qty = 1){
        const vm = this;
        let api = `${process.env.APIPATH}/api/${
            process.env.CUSTOMEPATH
        }/cart`;
        vm.status.loadingItem = id;
        const cart = {
            product_id: id,
            qty
        }
        vm.$http.post(api , { data:cart }).then(res => {
            vm.status.loadingItem = ''
            $('#productModal').modal('hide')
            this.getCart()
            this.$bus.$emit("messsage:push", '成功加入購物車', "success")
      });
    },
    delCart(id){
         const vm = this;
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMEPATH
      }/cart/${id}`;
      vm.status.loadingItem = id;
      vm.$http.delete(api).then(res => {
        vm.status.loadingItem = ''
        vm.getCart()
      });
    },
    getCart(flag = false){
        const vm = this;
        let api = `${process.env.APIPATH}/api/${
            process.env.CUSTOMEPATH
        }/cart`
        vm.isLoading = true
        vm.$http.get(api).then(res => {
            let num = 0
            vm.cartList = res.data.data
            // Array.prototype.forEach.call(vm.cartList.carts, item => {
            //     num += item.qty
            //     vm.cartLen = num
            // })
            if(vm.cartList.carts.length === 0){
              vm.cartLen = 0
            }
            vm.cartList.carts.forEach(item => {
               num += item.qty
               vm.cartLen = num
            })
            if(flag === true){
              $('#CartModal').modal('show')
            }
            vm.isLoading = false
        });
    },
    addCouponCode(){
        const vm = this;
        let api = `${process.env.APIPATH}/api/${
            process.env.CUSTOMEPATH
        }/coupon`
        const coupon = {
            code: vm.coupon_code
        }
        vm.isLoading = true
        vm.$http.post(api , {data: coupon }).then(res => {
           if(res.data.success){
               this.$bus.$emit("messsage:push", res.data.message, "success")
           }else if(res.data.message === '找不到優惠券!'){
               this.$bus.$emit("messsage:push", res.data.message, "danger")
           }else{
               this.$bus.$emit("messsage:push", res.data.message, "warning")
           }
           vm.isLoading = false
           vm.getCart()
        });
    },
    createOrder(){
      const vm = this
      const api = `${process.env.APIPATH}/api/${
            process.env.CUSTOMEPATH
        }/order`
      const order = vm.form
      // vm.isLoading = true
      this.$validator.validate().then((result) => {
        if (result) {
          vm.$http.post(api , { data : order }).then(res => {
            if(res.data.success){
              vm.$router.push(`/customer_checkout/${res.data.orderId}`)
            }
              vm.isLoading = false
          })
        }else{
          console.log('欄位不完整')
        }
      })
    }
  },
  created() {
    this.getAllProducts()
    // this.getProducts()
    this.getCart()
  },
  components: {
    Page
  }
};
</script>


