<template>
    <div>
        <loading :active.sync="isLoading"></loading>
            <table class="table">
                <thead>
                    <th>購買時間</th>
                    <th>Email</th>
                    <th>購買款項</th>
                    <th>應付金額</th>
                    <th>是否付款</th>
                </thead>
                <tbody>
                    <tr v-for="item in orderList" :key="item.id">
                    <td class="align-middle" v-if="item.is_paid === true">{{ item.paid_date }}</td>
                    <td class="text-danger"  v-else>{{ item.create_at }}</td>
                    <td class="align-middle">{{ item.user.email }}</td>
                    <td>
                        <p v-for="(items , key) in item.products" :key ="key">{{ items.product.title }} : {{ items.qty }} {{ items.product.unit }}</p>
                    </td>
                    <td class="text-center">{{ item.total | currency }}</td>
                    <td class="text-success" v-if="item.is_paid === true">已付款</td>
                    <td class="text-danger"  v-else>未付款</td>
                    </tr>
                </tbody>
             </table>
    </div>
</template>



<script>
export default {
    data(){
        return{
            orderList:{
                // products:{
                // }
            },
            productList:[],
            pagination:{},
            isLoading: false
        }
    },
    methods:{
        getOrderList(page = 1){
            const vm = this
            const api = `${process.env.APIPATH}/api/${
                        process.env.CUSTOMEPATH
                        }/admin/orders?page=${page}`
            vm.isLoading = true
            vm.$http.get(api).then(res =>{
                console.log(res.data)
                vm.orderList = res.data.orders
                vm.pagination = res.data.pagination
                // console.log(res.data.orders[0].products)
                vm.orderList.forEach(item => {
                    let transDate = vm.timetrans(item.paid_date)
                    let transDate2 = vm.timetrans(item.create_at)
                    item.paid_date = transDate
                    item.create_at = transDate2
                });
                vm.isLoading = false
            })
        },
        timetrans(date){
            let dates = new Date(date * 1000)
            let years = dates.getFullYear() + '-'
            let months = (dates.getMonth()+1 < 10 ? '0'+(dates.getMonth()+1) : dates.getMonth()+1) + '-'
            let Dates = (dates.getDate() < 10 ? '0' + (dates.getDate()) : dates.getDate())
            return years+months+Dates
        }
    },
    created(){
        this.getOrderList()
    }

}
</script>


